"use client"

import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Badge } from "@/components/ui/badge" // 추가
import { useEffect, useState, useCallback } from "react"
import {
  TrendingUp, Target, Calendar, MessageSquare,
  Settings, Wallet, PiggyBank, CreditCard, LogOut, Lock,
  Plus, Sparkles, Loader2, ShieldCheck,
  UserCheck, ChevronRight, History, Activity
} from "lucide-react"
import Image from "next/image"

// 은행명과 이미지를 매칭하는 객체
const BANK_LOGOS: Record<string, any> = {
  "신한은행": "/lib/images/shin.png",
  "기업은행": "/lib/images/ki.png",
  "국민은행": "/lib/images/kuk.png",
};

interface LinkedAccount {
  bankAccount: string;
  bankName: string;
  connectedId: string;
  accountId: string;
  isTransactionSync?: boolean;
}

export default function DashboardPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  const [linkedAccounts, setLinkedAccounts] = useState<LinkedAccount[]>([])
  const [isLinked, setIsLinked] = useState(false)
  const [isSynced, setIsSynced] = useState(false)
  const [userGoal, setUserGoal] = useState<any>(null)
  const [latestReport, setLatestReport] = useState<any>(null)
  const [reportCount, setReportCount] = useState(0)

  const fetchData = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")
    if (!token) { router.push("/login"); return }

    try {
      const storedName = localStorage.getItem("name")
      if (storedName) setUserName(storedName)

      // API 호출부 (기존 로직 유지)
      // ... (fetch 코드 생략)
      setIsLoading(false)
    } catch (error) {
      console.error("데이터 로드 실패:", error)
      setIsLoading(false)
    }
  }, [router])

  useEffect(() => { fetchData() }, [fetchData])

  const handleLogout = () => { localStorage.clear(); router.push("/"); }

  const CardBlurOverlay = ({ message, linkHref, icon: Icon = Lock }: any) => (
    <div className="absolute inset-0 z-10 flex flex-col items-center justify-center bg-background/60 backdrop-blur-[4px] p-4 text-center">
      <div className="bg-card p-5 rounded-2xl shadow-2xl border border-border max-w-[240px]">
        <Icon className="h-6 w-6 text-primary mx-auto mb-2" />
        <p className="text-[12px] font-black uppercase tracking-tight mb-3 leading-tight italic">{message}</p>
        <Link href={linkHref}><Button size="sm" className="w-full h-8 text-[11px] font-black uppercase rounded-lg">Link Now</Button></Link>
      </div>
    </div>
  )

  if (isLoading) return <div className="min-h-screen flex items-center justify-center bg-background"><Loader2 className="h-10 w-10 animate-spin text-primary" /></div>

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      {/* HEADER: Admin과 스타일 통일 */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-black text-[10px] italic">USER</Badge>
          </Link>
          <div className="flex items-center gap-3">
            <Link href="/dashboard/chat"><Button variant="ghost" size="icon" className="rounded-xl"><MessageSquare className="h-5 w-5" /></Button></Link>
            <Link href="/dashboard/settings"><Button variant="ghost" size="icon" className="rounded-xl"><Settings className="h-5 w-5" /></Button></Link>
            <Button variant="ghost" size="icon" onClick={handleLogout} className="rounded-xl hover:text-destructive"><LogOut className="h-5 w-5" /></Button>
            <Avatar className="h-9 w-9 border-2 border-primary/20 shadow-sm ml-1">
              <AvatarFallback className="bg-primary text-primary-foreground text-xs font-black uppercase">
                {userName?.charAt(0)}
              </AvatarFallback>
            </Avatar>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {/* 타이틀 영역: 볼드하게 */}
        <div className="flex justify-between items-end mb-8">
          <div>
            <h1 className="text-4xl font-black tracking-tighter uppercase italic">Dashboard</h1>
            <p className="text-muted-foreground font-medium mt-1">안녕하세요, <span className="text-foreground font-bold">{userName}</span>님의 재무 현황입니다.</p>
          </div>
          <Button variant="outline" size="sm" className="rounded-xl font-bold border-border shadow-none" onClick={() => fetchData()}>
            <Activity className="h-4 w-4 mr-2 text-primary" /> 리프레시
          </Button>
        </div>

        {/* 상단 요약 카드: Admin 스타일 적용 */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card className="p-6 flex flex-col h-[200px] border-border shadow-none rounded-2xl bg-secondary/10">
            <div className="flex items-center justify-between mb-4 border-b pb-3 border-border/50">
              <span className="text-[10px] font-black text-muted-foreground uppercase tracking-widest flex items-center gap-2">
                <ShieldCheck className="h-4 w-4 text-primary" /> My Accounts
              </span>
              <Button variant="ghost" size="icon" className="h-7 w-7 rounded-lg hover:bg-white" onClick={() => router.push("/dashboard/link")}>
                <Plus className="h-4 w-4" />
              </Button>
            </div>
            <div className="flex-1 overflow-y-auto space-y-2.5 pr-1 text-sm font-bold">
              {isLinked ? linkedAccounts.slice(0, 3).map((acc, i) => (
                <div key={i} className="flex justify-between items-center group">
                  <div className="flex items-center gap-2">
                    <div className="h-6 w-6 rounded-full bg-white border flex items-center justify-center text-[10px] font-black italic">{acc.bankName.charAt(0)}</div>
                    <span className="truncate max-w-[80px] text-xs uppercase tracking-tight">{acc.bankName}</span>
                  </div>
                  <span className="font-mono text-[11px] text-muted-foreground tracking-tighter">****{acc.bankAccount?.slice(-4)}</span>
                </div>
              )) : <div className="text-muted-foreground opacity-50 py-4 italic text-center text-xs">No linked accounts.</div>}
            </div>
          </Card>

          <Card className="p-6 h-[200px] relative overflow-hidden border-border shadow-none rounded-2xl">
            {!isLinked || !isSynced ? <CardBlurOverlay message="Waiting for Data" linkHref="/dashboard/link" /> : null}
            <div className="h-10 w-10 rounded-xl bg-primary/10 flex items-center justify-center mb-4"><Wallet className="h-5 w-5 text-primary" /></div>
            <p className="text-[10px] text-muted-foreground mb-1 font-black uppercase tracking-tighter italic">Total Capital</p>
            <p className="text-3xl font-black tracking-tighter">₩{isSynced ? "45,280,000" : Number(userGoal?.capital || 0).toLocaleString()}</p>
            <div className="absolute bottom-6 right-6 opacity-20"><TrendingUp className="h-8 w-8 text-primary" /></div>
          </Card>

          <Card className="p-6 h-[200px] relative overflow-hidden border-border shadow-none rounded-2xl">
            {!isLinked || !isSynced ? <CardBlurOverlay message="Waiting for Data" linkHref="/dashboard/link" /> : null}
            <div className="h-10 w-10 rounded-xl bg-green-500/10 flex items-center justify-center mb-4"><PiggyBank className="h-5 w-5 text-green-600" /></div>
            <p className="text-[10px] text-muted-foreground mb-1 font-black uppercase tracking-tighter italic">Monthly Savings</p>
            <p className="text-3xl font-black tracking-tighter text-green-600">₩1,200,000</p>
          </Card>

          <Card className="p-6 h-[200px] relative overflow-hidden border-border shadow-none rounded-2xl">
            {!isLinked || !isSynced ? <CardBlurOverlay message="Waiting for Data" linkHref="/dashboard/link" /> : null}
            <div className="h-10 w-10 rounded-xl bg-red-500/10 flex items-center justify-center mb-4"><CreditCard className="h-5 w-5 text-red-600" /></div>
            <p className="text-[10px] text-muted-foreground mb-1 font-black uppercase tracking-tighter italic">Monthly Spend</p>
            <p className="text-3xl font-black tracking-tighter text-red-600">₩2,450,000</p>
          </Card>
        </div>

        {/* 하단 메인 분석 영역 */}
        <div className="grid lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2">
            <Card className="relative overflow-hidden border-border shadow-none rounded-2xl bg-card p-8 min-h-[500px]">
              <div className="flex items-center justify-between mb-8 border-b pb-6 border-border/50">
                <h2 className="text-2xl font-black italic tracking-tighter flex items-center gap-2 uppercase">
                  <Sparkles className="h-7 w-7 text-primary animate-pulse" /> AI Financial Report
                </h2>
                <Link href="/dashboard/goal">
                  <Button variant="outline" size="sm" className="rounded-xl font-bold h-9">상세보기 <ChevronRight className="h-4 w-4 ml-1" /></Button>
                </Link>
              </div>
              
              <div className="space-y-10">
                <div className="space-y-4">
                  <h3 className="text-xs font-black uppercase tracking-[0.2em] text-muted-foreground flex items-center gap-2">
                    <Target className="h-4 w-4 text-primary" /> My Financial Goal
                  </h3>
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div className="p-5 rounded-2xl bg-secondary/30 border-none">
                      <p className="text-[9px] font-black text-muted-foreground uppercase mb-1.5 tracking-widest italic">Info</p>
                      <p className="text-base font-black tracking-tight">{userGoal?.job || "미입력"} · {userGoal?.age || "0"}세</p>
                    </div>
                    <div className="p-5 rounded-2xl bg-primary/5 border-none">
                      <p className="text-[9px] font-black text-primary uppercase mb-1.5 tracking-widest italic">Monthly Income</p>
                      <p className="text-base font-black tracking-tight text-primary">₩{Number(userGoal?.monthlyIncome || 0).toLocaleString()}</p>
                    </div>
                    <div className="p-5 rounded-2xl bg-red-500/5 border-none">
                      <p className="text-[9px] font-black text-red-600 uppercase mb-1.5 tracking-widest italic">Fixed Expenses</p>
                      <p className="text-base font-black tracking-tight text-red-600">₩{Number(userGoal?.fixedExpenses || 0).toLocaleString()}</p>
                    </div>
                  </div>
                </div>

                <div className="space-y-4">
                   <h3 className="text-xs font-black uppercase tracking-[0.2em] text-muted-foreground flex items-center gap-2">
                    <History className="h-4 w-4 text-primary" /> Analysis Summary
                   </h3>
                   {latestReport ? (
                     <div className="p-7 rounded-2xl border border-border bg-white shadow-sm space-y-5 relative">
                        <div className="absolute top-0 left-0 w-1.5 h-full bg-primary/30"></div>
                        <p className="text-sm font-bold leading-relaxed text-slate-700 italic">
                          "{latestReport.resultReport || "상세 분석 내용이 없습니다."}"
                        </p>
                        <Button variant="secondary" className="w-full font-black uppercase h-11 rounded-xl tracking-tighter" onClick={() => router.push("/dashboard/goal")}>View Full Report</Button>
                     </div>
                   ) : (
                     <div className="p-12 text-center border-2 border-dashed rounded-2xl border-border/50">
                        <p className="text-xs font-black text-muted-foreground mb-4 uppercase tracking-[0.3em] italic">No Reports Generated Yet.</p>
                        <Button size="lg" className="font-black rounded-xl uppercase tracking-tight" onClick={() => router.push("/dashboard/goal")}>Start AI Analysis</Button>
                     </div>
                   )}
                </div>

                <div className="pt-6 flex items-center justify-center">
                  <div className="flex items-center gap-3 px-8 py-3 rounded-2xl bg-primary/5 border border-primary/10">
                    <span className="text-[10px] font-black text-muted-foreground uppercase italic">Total Analysis Count</span>
                    <span className="text-2xl font-black text-primary tracking-tighter">{reportCount}</span>
                  </div>
                </div>
              </div>
            </Card>
          </div>

          <div className="space-y-6">
            <Card className="p-6 bg-secondary/10 border-none rounded-2xl">
              <h2 className="text-[10px] font-black mb-6 text-muted-foreground uppercase tracking-[0.3em] italic text-center">Quick Menu</h2>
              <div className="grid gap-3">
                <Link href="/dashboard/goal" className="block"><Button variant="outline" className="w-full justify-between h-14 font-black rounded-xl border-border hover:bg-white transition-all uppercase tracking-tight" size="lg"><span>Financial Goal</span><Target className="h-5 w-5 text-primary" /></Button></Link>
                <Link href="/dashboard/ai-chat" className="block"><Button variant="outline" className="w-full justify-between h-14 font-black rounded-xl border-border hover:bg-white transition-all uppercase tracking-tight" size="lg"><span>AI Consultation</span><MessageSquare className="h-5 w-5 text-primary" /></Button></Link>
                <Link href="/dashboard/chat" className="block"><Button variant="outline" className="w-full justify-between h-14 font-black rounded-xl border-border hover:bg-white transition-all uppercase tracking-tight" size="lg"><span>Expert Booking</span><UserCheck className="h-5 w-5 text-primary" /></Button></Link>
              </div>
            </Card>

            <Card className="p-6 border-border shadow-none rounded-2xl bg-accent/5 min-h-[200px]">
              <h2 className="text-xs font-black flex items-center gap-2 mb-6 uppercase tracking-widest"><Calendar className="h-4 w-4 text-accent" /> Appointments</h2>
              <div className="text-center py-10">
                 <p className="text-[11px] text-muted-foreground font-black uppercase tracking-tighter italic mb-4">No confirmed bookings.</p>
                 <Button variant="link" className="text-xs font-black uppercase underline p-0 h-auto" onClick={() => router.push("/dashboard/expert")}>Find an Expert</Button>
              </div>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}